@media (min-width: 320px) {
  body {
    color: #faebd7;
    background-color: #0f0f0f;
  }

  .breed-select {
    width: 90%;
    height: 48px;
    color: #faebd7;
    background-color: rgba(0, 0, 0, 0);
    border: 2px solid #faebd7;
    margin: 20px auto auto;
    font-size: 18px;
    display: none;
  }

  .breed-select:active, .breed-select:focus {
    background-color: #0f0f0f;
    outline: none;
  }

  .loader {
    width: 50px;
    height: 50px;
    border: 5px solid #faebd7;
    border-top-color: gray;
    border-radius: 50%;
    margin: 20px auto;
    animation: spin 1s linear infinite;
    display: none;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  .cat-info img {
    border: 2px solid #faebd7;
    margin: auto;
    display: flex;
  }

  .cat-info h2 {
    max-width: fit-content;
    margin: 20px auto;
    font-weight: 700;
    display: flex;
  }

  .cat-info h3, .cat-info p {
    max-width: fit-content;
    font-size: 16px;
    font-weight: 600;
    display: flex;
  }
}

@media (min-width: 1280px) {
  .breed-select {
    width: 400px;
    margin-top: 20px;
    margin-left: 30px;
  }

  .loader {
    margin-top: 2%;
    margin-left: 200px;
  }

  .cat-info img {
    margin-top: 20px;
    margin-left: 80px;
  }
}

